@use "./abstracts/" as *;

$backround: #f4f5f6;
$black: rgb(60, 60, 60);
$white: #f4f5f6;
$purple: blueviolet;
$dark-purple: rgb(98, 28, 164);


html {
    font-family:'Courier New', Courier, monospace;
    font-size: 16px;
    color: $black;

    @include media("<=phone"){
        font-size: 0.7rem;
        line-height: 1.5rem;
    }
}

body {
    height: 100vh;
    margin: 0;
    padding: 0;
    background: $backround;
    overflow-x: hidden;

    * {
        margin: 0;
        padding: 0;
    }
}

h1 {
    font-size: 3rem;
}

h2 {
    @include media("<=phone") {
        font-size: 1.5rem;
    }
}

img {
    display: block;
}

ul {
    list-style-type: none;
    padding: 0;
}

button {
    height: 2rem;
    width: 6rem;
    color: $purple;
    font-size: 0.8rem;
    letter-spacing: 1px;
    // font-weight: 600;
    background-color: $white;
    border: 1px solid $purple;
    transition: all 200ms ease-in-out;

    &:hover {
        color: $white;
        background-color: $dark-purple;
        border: 1px solid $white;
    }
}

a {
    text-decoration: none;
    color: $black;
    transition: all 200ms ease-in-out;

    &:hover {
        color: $purple;
    }
}

p {
    font-size: 16px;
    @include media("<=phone"){
        font-size: 12px;
        line-height: 1.5rem;
    }
}

.container {
    display: flex;
    flex-flow: column;
    padding: 0rem 2rem;
    height: 100%;

    @include media("<=phone") {
        padding: 0;
    }
}

.container-small {
    padding: 2rem 6rem;
}

.mobile-menu {
    display: none;

    @include media("<=tablet") {
        display: flex;
        flex-flow: column;
    }

    .menu-line {
        margin-block: 5px;
        display: block;
        width: 2rem;
        height: 4px;
        border-radius: 25px;
        background-color: $black;
    }
}

.prevent-scroll {
    overflow-y: hidden;
}

.content-shift-fix {
    margin-right:17px;
}